// Open Sans - Used for Headers, Headings, Titles and etc.. 
@font-face {
    font-family: 'Open Sans';
    font-weight: 400;
    font-style: normal;
    src: url('https://mohamed28.github.io/app/assets/fonts/open-sans-regular.ttf') format('truetype'),
        url('https://mohamed28.github.io/app/assets/fonts/open-sans-regular.woff') format('woff'),
        url('https://mohamed28.github.io/app/assets/fonts/open-sans-regular.woff2') format('woff2');
}

@font-face {
    font-family: 'Open Sans';
    font-weight: 600;
    font-style: normal;
    src: url('https://mohamed28.github.io/app/assets/fonts/open-sans-bold.ttf') format('truetype'),
        url('https://mohamed28.github.io/app/assets/fonts/open-sans-bold.woff') format('woff'),
        url('https://mohamed28.github.io/app/assets/fonts/open-sans-bold.woff2') format('woff2');
}

@font-face {
    font-family: 'Open Sans';
    font-weight: 800;
    font-style: normal;
    src: url('https://mohamed28.github.io/app/assets/fonts/open-sans-extra-bold.ttf') format('truetype'),
        url('https://mohamed28.github.io/app/assets/fonts/open-sans-extra-bold.woff') format('woff'),
        url('https://mohamed28.github.io/app/assets/fonts/open-sans-extra-bold.woff2') format('woff2');
}

